.main.style2>.content{-webkit-transition:-webkit-transform 1s ease;transition:-webkit-transform 1s ease;transition:transform 1s ease;transition:transform 1s ease, -webkit-transform 1s ease;-webkit-transform:translateZ(0);transform:translateZ(0);position:relative;width:35%;margin:0
}
.main.style2.left{-moz-justify-content:-moz-flex-start;-ms-justify-content:-ms-flex-start;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start
}
.main.style2.right{-moz-justify-content:-moz-flex-end;-ms-justify-content:-ms-flex-end;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end
}
.main.style2.inactive.left>.content{-webkit-transform:translateX(-100%);transform:translateX(-100%)
}
.main.style2.inactive.right>.content{-webkit-transform:translateX(100%);transform:translateX(100%)
}
.main.style3{text-align:center;padding:6em 0 6em 0
}
.main.style3 h2{color:#000;font-size:4.25em;line-height:1em
}
.main.style3 p{color:#000
}
.main.style3 .content>header{margin-bottom:2em
}
.main.style3.primary{background:#fff
}
.main.style3.secondary{background:#f5f6f7
}
.main.dark{color:#fff
}
.main.dark a{color:inherit
}
.main.dark .button.style2{border-color:#fff
}
.main.dark .button.style2:hover{background-color:rgba(255,255,255,.125)
}
.main.dark .button.style2:active{background-color:rgba(255,255,255,.25)
}
.main.dark .button.style2.down{background-image:url(data:image/svg+xml;base64,)
}
body.is-touch .main{background-attachment:scroll !important
}
@media screen and (max-width: 1920px){
.main.style2 .content{width:40%
}
}
@media screen and (max-width: 1280px){
.main.style2 .content{width:50%
}
}
@media screen and (max-width: 1000px){
.main.style2 .content{width:60%
}
}
@media screen and (max-width: 736px){
.main>.content br{display:none
}
.main.fullscreen{height:auto !important
}
.main.style1{padding:4em 15px 4em 15px
}
.main.style1 h2{font-size:3em
}
.main.style2{padding:6em 15px 6em 15px
}
.main.style2:before,.main.style2:after{display:none !important
}
.main.style2 .button.anchored{display:none
}
.main.style2 .content{width:100%;max-width:100%;text-align:center;-webkit-transform:none;transform:none
}
.main.style3{text-align:center;padding:3em 10px 3em 10px
}
}
@media screen and (max-width: 480px){
.main>.content{max-width:calc(100% - 1.5em)
}
}
